import { Container, Row, Col } from "react-bootstrap";
import { MailchimpForm } from "./MailchimpForm";
import logo from "../assets/img/logo.svg";
import navIcon1 from "../assets/img/nav-icon1.svg";
import navIcon2 from "../assets/img/nav-icon2.svg";
import navIcon3 from "../assets/img/nav-icon3.svg";

export const Footer = () => {
  return (
    <footer className="footer">
      <Container>
        <Row className="align-items-center">
          <MailchimpForm />
          <Col size={12} sm={6}>
            <img src={logo} alt="Logo" />
          </Col>
          <Col size={12} sm={6} className="text-center text-sm-end">
            <div className="social-icon">
              <a href="https://www.linkedin.com/in/evan-rasyid-82b645298/" target="_blank"><img src={navIcon1} alt="Iconlinked" /></a>
              <a href="https://www.tiktok.com/@panepunn" target="_blank"><img src={navIcon2} alt="Icon-tiktok" /></a>
              <a href="https://www.instagram.com/evanrasyidd/" target="_blank"><img src={navIcon3} alt="Icon-instagram" /></a>
            </div>
            <p>Created By Epun</p>
          </Col>
        </Row>
      </Container>
    </footer>
  )
}
